/* CSS */
.button-15 {
  background-color:rgb(131, 130, 130);
  border:none;
  border-radius: 4px;
  box-sizing: border-box;
  color: #FAFBFC;
  cursor: pointer;
  direction: ltr;
  display: block;
  font-family: "SF Pro Text","SF Pro Icons","AOS Icons","Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: -.022em;
  line-height: 1.47059;
  min-width: 20px;
  overflow: visible;
  padding: 4px 15px;
  text-align: center;
 
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
}

.button-15:disabled {
  cursor: default;
  opacity: .3;
}

.button-15:hover {
  background-color:#585858;
  border:none;
  text-decoration: none;
}

.button-15:active {
  
  border-color: #757575;
  outline: none;
}

.button-15:focus {
  box-shadow: rgba(208, 210, 211, 0.5) 0  0 3px 3px;
  outline: none;
  background-color:#585858;
}

